<template>
  <div>
    <el-card class="g_label">
      <el-row justify="space-between" type="flex" :gutter="20" class="oneRow">
        <el-col :span="24">
          <div>
            <div class="div_inline">
              所属企业:
              <el-select
                v-model="seekCompany"
                clearable
                size="small"
                @change="getUserList"
                @clear="getUserList"
                style="width:160px; margin-left: 10px;"
                placeholder="请选择企业"
              >
                <el-option
                  v-for="item in comlistdata"
                  :key="item.company_name"
                  :label="item.company_name"
                  :value="item.company_name"
                ></el-option>
              </el-select>
            </div>
            <div class="div_inline">
              用户名:
              <el-input
                size="small"
                clearable
                class="el-input_co"
                style="width:150px"
                placeholder="请输入用户名"
                v-model="seekName"
                @keyup.enter.native="getUserList"
                @clear="getUserList"
              />
            </div>
            <div class="div_inline">
              <!-- 筛选按钮 -->
              <el-button
                @click="getUserList"
                class="el-button_co"
                type="primary"
                icon="el-icon-search"
              >搜索</el-button>
            </div>
            <div class="div_inline div_right">总人数: {{pageTotal}}</div>
          </div>
        </el-col>
      </el-row>
      <el-row justify="space-between" type="flex" :gutter="20" class="twoRow">
        <el-col :span="24">
          <el-button-group>
            <!-- 添加按钮 -->
            <el-button
              @click="toAdduser"
              class="el-button_co"
              type="primary"
              icon="el-icon-circle-plus-outline"
            >添加</el-button>
            <!-- 删除 -->
            <el-button
              @click="isDel"
              class="el-button_co"
              type="danger"
              icon="el-icon-remove-outline"
            >删除</el-button>
            <!-- 分配权限 -->
            <el-button
              class="el-button_co el-button_coco"
              type="primary"
              icon="el-icon-s-custom"
            >分配权限</el-button>
            <!-- 导入 -->
            <el-button class="el-button_co" type="primary" icon="el-icon-upload2">导入</el-button>
            <!-- 导出 -->
            <el-button class="el-button_co" type="primary" icon="el-icon-download">导出</el-button>
          </el-button-group>
        </el-col>
      </el-row>

      <el-row>
        <!-- 表格 -->
        <el-table
          style="width: 100%"
          :header-cell-style="{background:'#eee',color:'#000'}"
          :data="listdata"
          @selection-change="handleSelectionChange"
          stripe
        >
          <el-table-column type="selection" width="55"></el-table-column>
          <el-table-column label="序号" align="center" type="index" width="55"></el-table-column>
          <el-table-column prop="username" label="用户名" align="center"></el-table-column>
          <el-table-column prop="company_name" label="所属企业" align="center"></el-table-column>
          <el-table-column prop="sex" label="性别" align="center"></el-table-column>
          <el-table-column prop="birthday" label="生日" align="center"></el-table-column>
          <el-table-column prop label="操作" align="center">
            <template slot-scope="scope">
              <el-button-group>
                <el-button size="mini" icon="el-icon-edit" @click="isEdit(scope)"></el-button>
                <el-button size="mini" icon="el-icon-picture-outline" @click="isImage(scope)"></el-button>
                <el-button size="mini" icon="el-icon-document" @click="isInfo(scope)"></el-button>
                <el-button size="mini" icon="el-icon-delete" @click="isDel(scope)"></el-button>
              </el-button-group>
            </template>
          </el-table-column>
        </el-table>
      </el-row>
      <!-- 分页 -->
      <el-row>
        <div class="page_div">
          <el-pagination
            background
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page.sync="currentPage"
            :page-size="pageSize"
            layout="total, prev, pager, next"
            :total="pageTotal"
          ></el-pagination>
        </div>
      </el-row>
    </el-card>

    <!-- 添加对话框： -->
    <el-dialog
      title="添加人员信息"
      :visible.sync="adduser.dialogVisible"
      :destroy-on-close="true"
      width="680px"
    >
      <el-form
        v-loading="diaLoading"
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-row>
          <el-col :span="15">
            <el-form-item label="用户名" prop="username" autocomplete="off">
              <el-input
                size="small"
                placeholder="请输入用户名"
                v-model="ruleForm.username"
                :maxlength="12"
                :readonly="readOnlyFlag"
                @focus="readOnlyFlag = false;"
                autocomplete="new-accounts"
                style="width:200px;"
              ></el-input>
            </el-form-item>
            <el-form-item label="密码" prop="password">
              <!-- <input name="a" type="password" placeholder="密码" style="position:fixed;bottom:-9999px" /> -->
              <input
                name="a"
                type="password"
                placeholder="密码"
                style="position:absolute;left: -9999999px;"
              />
              <el-input
                show-password
                size="small"
                placeholder="请输入密码"
                v-model="ruleForm.password"
                :maxlength="24"
                :readonly="readOnlyFlag"
                @focus="readOnlyFlag = false;"
                autocomplete="new-passowrd"
                style="width:200px;"
              ></el-input>
            </el-form-item>
            <el-form-item label="所属企业" prop="company_name">
              <el-select
                v-model="ruleForm.company_name"
                clearable
                size="small"
                style="width:200px;"
                placeholder="请选择所属企业"
              >
                <el-option
                  v-for="item in comlistdata"
                  :key="item.company_name"
                  :label="item.company_name"
                  :value="item.company_name"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="性别" prop="sex">
              <el-radio-group v-model="ruleForm.sex">
                <el-radio label="男" value="男"></el-radio>
                <el-radio label="女" value="女"></el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item label="生日" required>
              <el-date-picker
                type="date"
                size="small"
                placeholder="选择日期"
                v-model="ruleForm.birthday"
                style="width: 200px;"
              ></el-date-picker>
            </el-form-item>
          </el-col>
          <el-col :span="9">
            <div class="upPicture" style="margin: 10px 0 0 0px;">
              <span style="color: #ff0000;">*</span>
              <span>照片:</span>
              <el-upload
                class="avatar-uploader"
                ref="upload"
                :with-credentials="true"
                :limit="1"
                :file-list="fileList"
                :data="myData()"
                :action="uploadUrl()"
                accept="image/jpeg"
                :headers="myHeader"
                :on-change="addFile"
                :on-remove="handleRemove"
                :on-success="upHandSuccess"
                :on-error="upHandError"
                :on-progress="upHandProgress"
                :auto-upload="false"
                style="margin-top: 10px;"
              >
                <div
                  class="avatar-uploader-icon div-bgphoto"
                  :style="{backgroundImage:'url(' + imageUrl + ')',width:uImgWidth,height:uImgHeight}"
                  @mouseover="selectStyle(true)"
                  @mouseout="selectStyle(false)"
                  @click="removeFile"
                >
                  <i
                    v-if="!imageUrl"
                    class="el-icon-plus avatar-uploader-icon"
                    :style="{width:uImgWidth,height:uImgHeight}"
                  ></i>
                  <i v-else :class="delicoClass" :style="{width:uImgWidth,height:uImgHeight}"></i>
                </div>
              </el-upload>
            </div>
          </el-col>
        </el-row>

        <el-divider></el-divider>
        <el-form-item style="padding-left: 135px; margin: -10px 0 5px 0">
          <el-button
            type="primary"
            @click="insert('ruleForm')"
            size="small"
            :icon="butIcoSubmit"
            :disabled="butDisabledSubmit"
          >立即添加</el-button>
          <el-button @click="resetForm('ruleForm')" size="small" icon="el-icon-refresh-left">重置</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

    <!-- 编辑对话框 -->
    <el-dialog title="修改人员信息" :visible.sync="edituser.dialogVisible" width="28%">
      <div class="titleMessage">1.基本信息</div>
      <div class="basicMessage">
        <div>
          <b>id:</b>
          <em>
            <el-input
              size="small"
              clearable
              placeholder="请输入id"
              v-model="adduser.id"
              :disabled="true"
            ></el-input>
          </em>
        </div>
        <div>
          <b>用户名:</b>
          <em>
            <el-input size="small" clearable placeholder="请输入用户名" v-model="adduser.username"></el-input>
          </em>
        </div>
        <div>
          <b>性别:</b>
          <em>
            <el-select
              v-model="adduser.sex"
              slot="prepend"
              placeholder="请输入性别"
              style="left:-0.75rem"
            >
              <el-option label="女" value="女"></el-option>
              <el-option label="男" value="男"></el-option>
            </el-select>
          </em>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="adduser.dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="isEdituser">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    var checkName = (rule, value, callback) => {
      if (!value) {
        return callback(new Error('请输入用户名'))
      }
      var len = 0
      for (var i = 0; i < value.length; i++) {
        var c = value.charCodeAt(i)
        // 单字节加1
        if ((c >= 0x0001 && c <= 0x007e) || (c >= 0xff60 && c <= 0xff9f)) {
          len++
        } else {
          len += 2
        }
      }
      if ((len < 4 && len > 0) || len > 12) {
        // 重点重点，下面就是填写提示的文字
        callback(new Error('名称长度为4-12个字符，一个中文字等于2个字符。'))
      } else {
        callback()
      }
    }
    return {
      readOnlyFlag: true,
      saveing: false, // 是否正在保存
      // 列表信息
      listdata: [],
      // 企业列表数据
      comlistdata: [],
      // 企业总数
      comTotal: 0,
      // 筛选功能表单数据
      seekCompany: '',
      seekName: '',
      // 添加人员和编辑用户
      adduser: {
        // 弹框
        dialogVisible: false,
        // 弹出日历
        isbirthData: false
      },
      // 修改人员信息
      edituser: {
        dialogVisible: false
      },
      fileList: [],
      dialogImageUrl: '',
      dialogVisible: false,
      UploadUrl: 'http://localhost:8080/',
      delId: [],
      currentPage: 1,
      pageSize: 11,
      pageTotal: 0,

      diaLoading: false,
      butIcoSubmit: 'el-icon-upload',
      butDisabledSubmit: false,
      delicoVisible: false,
      imageUrl: '',
      fileSizeIsSatisfy: true,
      uImgWidth: '110px',
      uImgHeight: '110px',

      ruleForm: {
        username: '',
        password: '',
        company_name: '',
        sex: '',
        birthday: ''
      },
      rules: {
        username: [
          // validator是自定义校验固定写法,我们只需填他的值(checkName)就好了， callback 必须被调用。
          {
            required: true,
            trigger: 'blur',
            validator: checkName
          }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 6, max: 24, message: '长度在 6 到 24 个字符', trigger: 'blur' }
        ],
        company_name: [
          { required: true, message: '请选择所属企业', trigger: 'change' }
        ],
        sex: [{ required: true, message: '请选择性别', trigger: 'change' }],
        birthday: [
          {
            type: 'date',
            required: true,
            message: '请选择日期',
            trigger: 'change'
          }
        ]
      }
    }
  },
  computed: {
    myHeader() {
      return {
        // authToken: window.sessionStorage.getItem('authToken')
        token: this.$Storage.localGet('token')
      }
    },
    delicoClass() {
      if (this.delicoVisible) {
        return 'el-icon-delete avatar-uploader-icon upimg-del-ico-show'
      } else {
        return 'el-icon-delete avatar-uploader-icon upimg-del-ico-hide'
      }
    }
  },
  created() {
    this.getUserList()
    this.getComList()
    // // 返回 web 主机的域名，如：http://127.0.0.1:8080/testdemo/test.html?id=1&name=test
    // var url = window.location.href
    // // 返回当前页面的路径和文件名，如：/testdemo/test.html
    // var pathname = window.location.pathname
    // // 返回 web 主机的端口，如：8080
    // var port = window.location.port
    // // 返回所使用的 web 协议，如：http:
    // var protocol = window.location.protocol
    // // 获取参数，如：?id=1&name=test
    // var search = window.location.search
    // // 获取指定参数值
    // console.log(url)
  },
  methods: {
    // 获取人员信息
    getUserList() {
      var apiUrl =
        '/api' +
        '/user/find/list' +
        '?page=' +
        this.currentPage +
        '&pageSize=' +
        this.pageSize
      if (this.seekCompany) {
        apiUrl += '&company_name=' + this.seekCompany
      }
      if (this.seekName) {
        apiUrl += '&username=' + this.seekName
      }

      this.$http
        .get(apiUrl)
        .then(res => {
          this.listdata = res.data.data.list
          this.pageTotal = res.data.data.total
        })
        // eslint-disable-next-line handle-callback-err
        .catch(err => {
          console.log('获取人员信息失败')
        })
    },
    // 获取公司列表：
    getComList() {
      var apiUrl =
        '/api' + '/company/find/list' + '?page=' + 1 + '&pageSize=' + 10
      this.$http
        .get(apiUrl)
        .then(res => {
          this.comlistdata = res.data.data.list
          this.comTotal = res.data.data.total
        })
        // eslint-disable-next-line handle-callback-err
        .catch(err => {
          console.log('获取公司列表数据失败')
        })
    },

    // 按钮触发-添加人员信息：
    toAdduser() {
      this.intSimData()
      this.adduser.dialogVisible = true
    },

    // 编辑修改用户
    isEdit(scope) {
      this.edituser.dialogVisible = true
      console.log(scope)
      this.adduser.id = scope.row.user_id
      this.adduser.username = scope.row.username
      this.adduser.sex = scope.row.sex
      this.adduser.AbirthData = scope.row.birthday
    },
    // 确定编辑按钮
    isEdituser() {
      this.$http
        .post('/api' + '/user/update', {
          user_id: this.adduser.id,
          username: this.adduser.username,
          sex: this.adduser.sex
        })
        .then(res => {
          console.log(res)
        })
        // eslint-disable-next-line handle-callback-err
        .catch(err => {
          console.log('获取公司信息失败')
        })
      this.edituser.dialogVisible = false
      this.getUserList()
    },
    // 查看图像
    isImage() {},
    // 预览详细信息接口
    isInfo() {},
    // 批量删除前面的复选框
    handleSelectionChange(val) {
      val.forEach((ele, indx) => {
        this.delId.push(ele.user_id)
      })
    },
    // 删除人员按钮
    async isDel(scope) {
      if (scope.row) {
      } else if (!this.delId.length) {
        this.$message({ message: '请您选择您需要删除的人员!', type: 'warning' })
        return false
      }
      const confirmResult = await this.$confirm(
        '您确定要删除吗?',
        '删除人员信息',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }
      ).catch(err => err)
      if (confirmResult !== 'confirm') {
        return this.$message.info('已取消删除')
      }
      if (scope.row) {
        this.delId.push(scope.row.user_id)
      }
      this.$http
        .post('/api' + '/user/del', this.delId)
        .then(res => {
          console.log(res)
        })
        // eslint-disable-next-line handle-callback-err
        .catch(err => {
          console.log('删除人员信息失败')
        })
      this.getUserList()
    },
    // 分页
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`)
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`)
      this.currentPage = val
      this.getUserList(this.currentPage)
    },

    // 重置表单：
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.intSimData()
    },

    // 设置上传图片删除图标显示
    selectStyle(_show) {
      this.delicoVisible = _show
      // console.log('selectStyle:', this.delicoVisible)
    },
    // 上传api地址
    uploadUrl: function() {
      // return 'http://127.0.0.1:8081/uploadFile'
      return '/api' + '/user/save'
    },
    // 加载本地图片：
    addFile(file, fileList) {
      console.log('addFile file:', file)
      console.log('addFile fileList:', fileList)
      this.fileList = fileList
      // 限制上传文件为5M
      // var sizeIsSatisfy = file.size < 5 * 1024 * 1024 ? true : false
      var sizeIsSatisfy = file.size / 1024 < 20
      console.log('addFile sizeIsSatisfy:', sizeIsSatisfy, file.size)

      var _this = this
      var eventR = event || window.event
      if (!eventR || !eventR.target || !eventR.target.files) {
        return
      }
      var fileR = eventR.target.files[0]
      var reader = new FileReader() // 转base64
      reader.onload = function(e) {
        _this.imageUrl = e.target.result // 将图片路径赋值给src
        console.log('e.target:', e.target)
        var image = new Image()
        image.src = e.target.result
        image.onload = function() {
          console.log('图片的宽度为' + this.width + ', 长度为' + this.height)
          console.log(
            '_this.uImgWidth: ' + _this.uImgWidth + ', ' + _this.uImgHeight
          )
          var myS = this.width / this.height
          var myW = 0
          var myH = 0
          if (myS >= 1) {
            myW = 110
            myH = Math.round(myW / myS)
          } else {
            myH = 110
            myW = Math.round(myH * myS)
          }
          _this.uImgWidth = myW + 'px'
          _this.uImgHeight = myH + 'px'
          console.log(
            '_this.uImgWidth: ' + _this.uImgWidth + ', ' + _this.uImgHeight
          )
        }
      }
      reader.readAsDataURL(fileR)

      this.fileSizeIsSatisfy = true
      if (sizeIsSatisfy) {
        return true
      } else {
        this.fileSizeIsSatisfy = false
        return false
      }
    },
    // 初始化提交数据：
    intSimData() {
      this.ruleForm = {
        username: '',
        password: '',
        company_name: '',
        sex: '',
        birthday: ''
      }
      this.removeFile()
    },
    // 清除上传和显示的图片：
    removeFile() {
      if (this.imageUrl || (this.fileList && this.fileList.length > 0)) {
        this.$refs.upload.clearFiles()
        this.imageUrl = ''
        this.fileList = []
        this.fileSizeIsSatisfy = true
        this.uImgWidth = '110px'
        this.uImgHeight = '110px'
      }
    },
    // 删除触发事件：
    handleRemove(file, fileList) {
      this.removeFile()
    },
    // 提交和上传：
    insert(formName) {
      // var _this = this
      this.$refs[formName].validate(valid => {
        if (valid) {
          if (this.fileList.length <= 0) {
            this.$message({
              message: '请上传人脸照片',
              type: 'warning'
            })
            return
          }
          if (!this.fileSizeIsSatisfy) {
            this.$message({
              message: '上传失败！文件大小不得超过20KB！',
              type: 'warning'
            })
            return
          }
          this.udShowStyle(true)
          // 手动上传文件，在点击确认的时候 校验通过才会去请求上传文件的url
          this.$refs.upload.submit()
        } else {
          // this.$message.error('error submit!!')
          this.$message.error('添加人员信息失败！')
          return false
        }
      })
    },
    upHandSuccess(response, file, fileList) {
      this.udShowStyle(false)
      this.adduser.dialogVisible = false
      this.$message({
        message: '添加人员信息成功！',
        type: 'success'
      })
      this.getUserList()
    },
    upHandError(err, file, fileList) {
      this.udShowStyle(false)
      this.$refs.upload.abort()
      this.adduser.dialogVisible = false
      this.$message.error('添加人员信息失败：' + ' [' + err.status + ']')
    },
    upHandProgress(event, file, fileList) {
      // console.log('upHandProgress event:', event)
      // console.log('upHandProgress file:', file)
      // console.log('upHandProgress fileList:', fileList)
    },
    // 提交开始或结束
    udShowStyle(_start) {
      if (_start) {
        this.butIcoSubmit = 'el-icon-loading'
        this.butDisabledSubmit = true
        this.diaLoading = true
      } else {
        this.intSimData()
        // this.removeFile()
        this.butIcoSubmit = 'el-icon-upload'
        this.butDisabledSubmit = false
        this.diaLoading = false
      }
    },
    // 上传照片时要提交的用户数据：
    myData() {
      // 检验并格式化生日：
      var sData = new Date(this.ruleForm.birthday)
      console.log(
        'sData:',
        sData,
        sData.getFullYear(),
        isNaN(sData.getFullYear())
      )
      if (isNaN(sData.getFullYear())) {
        this.ruleForm.birthday = new Date().format('yyyy-MM-dd')
      }
      var sDataH = new Date(this.ruleForm.birthday)
      console.log(
        'sDataH 222:',
        sData,
        sDataH.getFullYear(),
        isNaN(sDataH.getFullYear())
      )
      return {
        username: this.ruleForm.username,
        password: this.ruleForm.password,
        company_name: this.ruleForm.company_name,
        sex: this.ruleForm.sex,
        birthday: new Date(this.ruleForm.birthday).format('yyyy-MM-dd')
      }
    },
  }
}
</script>
